.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 8vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

}

#navbar {
  list-style-type: none;
  display: inline;

}

.App-link {
  color: #61dafb;
}

li {
 display: inline;
 padding: 20px;
}

.about-me {
  min-height: 40vh;
}
/* .map-container {
  min-height: 40vh;
} */

.projects {
  min-height: 100vh;
}
.contact {
  min-height: 40vh;
}

.about-me-text {
  padding: 40px;
  word-wrap: normal;
}

/* header section start */
header {
  padding: 20px 35px;
  background-color: var(--primary-color);
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-position: 80%;
  z-index: 9999;

}

header a {
  text-decoration: none; 
  color: var(--tertiary-color);
}
header nav {
  margin: 7px  0;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

header nav ul li {
  display:inline;
}

header nav ul li a {
  /* space header options */
  padding: 10px 15px;
  font-weight: normal;
  font-size: 20px;
}

header nav ul li a:hover {
  background: var(--tertiary-color);
  color: var(--primary-color);
  border-radius: 15px;
  text-shadow: none;
}

ul {
  /* remove bullets from the list */
  list-style-type: none;

}
/* header section end */

