/* about me section */
#about-me {
  background-color: var(--primary-color);
  padding: 20px;
}

.column-flow {
  display: flex;
  flex-flow: column wrap;
  text-align: center;
  align-items: center;

}

.about-me-img {
  background-image: url("./components/Projects/assets/img/dt_seattle.jpg");
  border-radius: 50%;
  border: 2px solid wheat;
  background-size: cover;
  justify-self: center;
  height: 150px;
  width: 150px;
  padding: 20px;
}

.about-me-text {
  display: flex;
  flex: 2 30%;
  flex-wrap: wrap;
  padding: 20px;
}
