/* project section */
#projects {
  padding: 20px;
  background-color: var(--tertiary-color);
}
.projects-container {
  display: flex;
  flex-flow: row wrap;
  align-content: center;
  display: inline-flex;
  text-align: center;

}

.project {
  display: flex;
  flex-flow: column wrap;
  flex: 2 50%;
  align-content: center;
  
}
/* .project a {

} */

.project h3 {
  text-align: center;
  padding: 20px;
}

.project a {
  color: black; 
}

/* figure out way to only add opacity to img and not text */
