.prj-img {
  height: 55vh;
  width: 55vh;
  border-radius: 50%;  
}

.prj-img:hover {
  height: 55vh;
  width: 55vh;
  border-radius: 50%;
  filter: grayscale(40%);
  filter: drop-shadow(0 0 0.75rem rgb(233, 170, 33));
}

.project-info {
  display: flex;
  flex-direction: column;

}

.project-text {
  padding: 50px;
  font-size: 18px;
  font-weight: bold;
  opacity: 0%;
  /* height: 55vh;
  width: 55vh; */
  border-radius: 50%;
}

.project-text:hover {
  opacity: 100%;
  color: antiquewhite;
  transition: opacity 1s ease-in-out;
  /* background: rgba(16, 54, 57, 0.5); */
  border-radius: 50%;
  background: rgba(16, 54, 57, 0.9);
  /* height: 55vh;
  width: 55vh; */
}


